<template>
  <div class = "picspace">
    <h2>{{this.$store.state.archTitle}}</h2>
    <div class="container">
      <figure v-for="arch in archList" :key="arch" class = "pic" @click="openModal(arch)">
          <img :src="require(`@/assets/${arch}`)" alt="풍경사진">
          <figcaption>{{getName(arch, 'arch')}}</figcaption>
      </figure>
    </div>
    <div v-if="showModal" class="modal">
        <div class="modal-content">
          <span class="close" @click="closeModal">&times;</span>
          <img :src="modalImage" alt="풍경사진" class="modal-image">
        </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showModal: false,
      modelImage: ''
    }
  },
  computed: {
    archList () {
      return this.$store.state.archList.map(item => item.url)
    }
  },
  methods: {
    getName (text) {
      return this.$store.state.archList.find(item => item.url === text)?.name || ''
    },
    openModal (image) {
      console.log(image)
      this.modalImage = require(`@/assets/${image}`)
      this.showModal = true
    },
    closeModal () {
      this.showModal = false
    }
  }
}
</script>
