<template>
    <div class = "picspace">
        <div v-if="hasPic" class ="container">
            <figure v-for="arch in searchArchlist" :key="arch" class = "pic" @click="openModal(arch.url)">
                <img :src="require(`@/assets/${arch.url}`)" alt="풍경사진">
                <figcaption>{{getName(arch.url, 'arch')}}</figcaption>
            </figure>
            <figure v-for="obj in searchObjlist" :key="obj" class = "pic" @click="openModal(obj.url)">
                <img :src="require(`@/assets/${obj.url}`)" alt="풍경사진">
                <figcaption>{{getName(obj.url, 'obj')}}</figcaption>
            </figure>
            <figure v-for="scene in searchScenelist" :key="scene" class = "pic" @click="openModal(scene.url)">
                <img :src="require(`@/assets/${scene.url}`)" alt="풍경사진">
                <figcaption>{{getName(scene.url, 'scene')}}</figcaption>
            </figure>
        </div>
        <div v-if="!hasPic">
          <nothingView/>
        </div>
        <div v-if="showModal" class="modal">
          <div class="modal-content">
            <span class="close" @click="closeModal">&times;</span>
            <img :src="modalImage" alt="풍경사진" class="modal-image">
          </div>
        </div>
    </div>
</template>

<script>
import nothingView from '@/views/NothingView.vue'

export default {
  components: {
    nothingView
  },
  data () {
    return {
      showModal: false,
      modelImage: ''
    }
  },
  computed: {
    searchArchlist () {
      return this.$store.getters.filteredArchlist
    },
    searchObjlist () {
      return this.$store.getters.filteredObjlist
    },
    searchScenelist () {
      return this.$store.getters.filteredScenelist
    },
    hasPic () {
      return (this.searchArchlist.length > 0 ||
        this.searchObjlist.length > 0 ||
        this.searchScenelist.length > 0)
    }
  },
  methods: {
    getName (text, type) {
      switch (type) {
        case 'arch':
          return this.$store.state.archList.find(item => item.url === text)?.name || ''
        case 'obj' :
          return this.$store.state.objList.find(item => item.url === text)?.name || ''
        case 'scene' :
          return this.$store.state.sceneList.find(item => item.url === text)?.name || ''
      }
    },
    openModal (image) {
      console.log(image)
      this.modalImage = require(`@/assets/${image}`)
      this.showModal = true
    },
    closeModal () {
      this.showModal = false
    }
  }
}
</script>
