<template>
  <div class = "picspace">
    <h2>{{this.$store.state.objTitle}}</h2>
    <div class ="container">
      <figure v-for="obj in objList" :key="obj" class = "pic" @click="openModal(obj)">
          <img :src="require(`@/assets/${obj}`)" alt="풍경사진">
          <figcaption>{{getName(obj)}}</figcaption>
      </figure>
    </div>
    <div v-if="showModal" class="modal">
        <div class="modal-content">
          <span class="close" @click="closeModal">&times;</span>
          <img :src="modalImage" alt="풍경사진" class="modal-image">
        </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showModal: false,
      modelImage: ''
    }
  },
  computed: {
    objList () {
      return this.$store.state.objList.map(item => item.url)
    }
  },
  methods: {
    getName (text) {
      return this.$store.state.objList.find(item => item.url === text)?.name || ''
    },
    openModal (image) {
      console.log(image)
      this.modalImage = require(`@/assets/${image}`)
      this.showModal = true
    },
    closeModal () {
      this.showModal = false
    }
  }
}
</script>
